.page {
  font-family: Roboto, sans-serif;
}

.page__body {
  background: #eee8e8 50%;
  margin: 0;
  padding: 0;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.header__logo {
  width: 40px;
  height: 40px;
  display: block;
}

.nav__list {
  justify-content: space-between;
  align-items: center;
  margin: 0;
  list-style: none;
  display: flex;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.nav__link {
  color: #060b35;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: flex;
}

.nav__link--blue:hover {
  color: #00acdc;
  transition: color .3s;
}

.nav .is-active {
  color: #00acdc;
  position: relative;
}

.nav .is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}

.nav .is-active:active {
  color: #fff;
}

.catalog {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  display: grid;
}

@media (width >= 488px) {
  .catalog {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .catalog {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .catalog {
    grid-template-columns: repeat(4, 200px);
  }
}

.stars {
  justify-content: center;
  align-items: center;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:last-child {
  margin-right: 0;
}

.stars--1 :nth-child(-n+1), .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4), .stars--5 :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.goodsCard {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  justify-content: space-between;
  width: 200px;
  padding: 0 16px 16px;
  transition: transform .3s;
  display: flex;
}

.goodsCard:hover {
  transform: scale(1.2);
}

.goodsCard__image {
  width: 160px;
  height: 134px;
  margin: 32px 3px 0;
}

.goodsCard__title {
  color: #060b35;
  width: 166px;
  margin: 40px 0 0;
  font-family: inherit;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.goodsCard:hover .goodsCard__title {
  color: #34568b;
}

.goodsCard__article {
  color: #616070;
  margin: 4px 0 0;
  font-size: 10px;
  line-height: 14px;
}

.goodsCard__rating {
  text-align: center;
  justify-content: space-between;
  align-items: center;
  width: 166px;
  height: 16px;
  margin: 16px 0 0;
  font-size: 10px;
  line-height: 14px;
  display: flex;
}

.goodsCard__price {
  text-align: center;
  justify-content: space-between;
  align-items: center;
  width: 166px;
  margin: 24px 0 0;
  line-height: 18px;
  display: flex;
}

.goodsCard__note {
  margin: 0;
  font-size: 12px;
  line-height: 18px;
}

.goodsCard__value {
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.goodsCard__button {
  box-sizing: border-box;
  color: #fff;
  text-transform: uppercase;
  cursor: pointer;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 40px;
  margin: 16px 0 0;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition: transform .3s;
  display: flex;
}

.goodsCard__button:hover {
  color: #00acdc;
  background-color: #fff;
}

.container {
  margin-top: 10px;
  margin-bottom: 32px;
  padding: 40px 50px;
}

